@import "https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Outfit, sans-serif;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #2f415b;
  place-items: center;
  display: grid;
}

.card {
  width: 21rem;
  background-image: linear-gradient(#14253dbf, #14253d);
  border-radius: 1rem;
  padding: 1.5rem 1.75em;
}

@media (min-width: 1440px) {
  .card {
    width: 20.5rem;
    padding: 1.5rem;
  }
}

.card__header-img {
  width: 100%;
  border-radius: .65rem;
}

.card__header {
  color: #fff;
  margin: 1.25rem 0 .75rem;
  font-size: 1.4rem;
  font-weight: 400;
}

@media (min-width: 1440px) {
  .card__header {
    font-size: 1.3rem;
  }
}

.card__paragraph {
  color: #8bacda;
  margin: 0 0 1rem;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.65;
}

@media (min-width: 1440px) {
  .card__paragraph {
    line-height: 1.5;
  }
}

.card__info {
  border-bottom: 1px solid #2f415b;
  justify-content: space-between;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  display: flex;
}

.card__info div {
  align-items: center;
  gap: .4rem;
  display: flex;
}

.card__info h3 {
  font-size: .925rem;
}

.card__info--price {
  color: #00fff8;
  letter-spacing: .2px;
  font-weight: 600;
}

.card__info--time {
  color: #8bacda;
  font-weight: 300;
}

.card__footer {
  height: 2.055rem;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.card__footer--img {
  width: 2.055rem;
  border: 1px solid #fff;
  border-radius: 100%;
}

.card__footer--text {
  color: #8bacda;
  font-size: .95rem;
  font-weight: 300;
  line-height: 1.65;
}

